import React from 'react'
import get from 'lodash/get'
import Helmet from 'react-helmet'

import Bio from '../components/Bio'

class SiteIndex extends React.Component {
  render() {
    const siteTitle = get(this, 'props.data.site.siteMetadata.title')
    const bio = get(this, 'props.data.bioMarkdown.edges[0].node.html')
    return <React.Fragment>
        <Helmet title={siteTitle} />
        <p>
          Sorry, but this one's not finished yet. Remind me to do that the
          next time you see me.
        </p>
        <footer>
          <Bio bio={bio} />
        </footer>
      </React.Fragment>
  }
}

export default SiteIndex

export const pageQuery = graphql`
  query FourOhFourQuery {
    site {
      siteMetadata {
        title
      }
    }
    bioMarkdown: allMarkdownRemark(
      filter: {
        fileAbsolutePath: { regex: "//partials/", ne: "/" }
        frontmatter: { title: { regex: "/bio/i" } }
      }
    ) {
      edges {
        node {
          html
          frontmatter {
            title
            content
          }
        }
      }
    }
  }
`



